.index-sc3
	margin-bottom: 45px
	@media screen and ( max-width: 1024px )
		display: none
	.category-container
		width: 80%
		margin: 0 auto
		@media screen and ( max-width: 1440px )
			width: 100%
		.box-cate
			display: flex
			flex-direction: column
			justify-content: center
			align-items: center
			height: 250px
			.img
				width: 115px
				height: 97px
				margin-bottom: 5px
				img 
					width: 100%
					height: 100%
			.title-box
				-webkit-tap-highlight-color: transparent
				color: #575757
				cursor: pointer
				font-size: 20px
				font-weight: 500
				letter-spacing: .0125em
				text-align: center
				display: block
				margin-bottom: 28px
			.end-box
				width: 100%
				display: flex
				justify-content: space-around
				.box-left
					display: flex
					align-items: center
					span 
						font-size: 17.5px
						margin-right: 2px
					p 
						color: #616161
						font-size: 14px
						line-height: 21px
						overflow: hidden
						text-overflow: ellipsis
						white-space: nowrap
						cursor: pointer
				.box-right
					display: flex
					align-items: center
					span 
						font-size: 24px
						color: #616161
						cursor: pointer
						margin-right: 5px
					p
						font-size: 14px
						color: #616161
						font-weight: 500
						cursor: pointer

				

