.main-danh-muc
	.box-right
		.box-sp 
			margin-bottom: 17px
		.content-box
			border: 1px solid #e5e5e5
			padding: 32px
			max-height: 345px
			overflow: hidden
			position: relative
			border-radius: 4px
			display: block
			margin-top: 20px
			&.toggle
				max-height: none
				overflow: visible
				padding-bottom: 74px
				background-image: none
				.moreContent 
					background-image: none
			.inner-content
				img 
					margin: 0 auto
					display: block
				p 
					margin-bottom: 20px
					display: block
				h1 
					font-weight: 700
					font-size: 25px
					text-align: center
					display: block
			.moreContent
				text-align: center
				position: absolute
				left: 0
				bottom: 0
				width: 100%
				display: flex
				align-items: center
				justify-content: center
				cursor: pointer
				background-image: linear-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255) 55%)
				padding: 77px 0px 24px
				text-align: center
				span 
					text-align: center
					color: #1a4bb7
					cursor: pointer
					display: inline-block
					font-size: 16px
					text-align: center
					vertical-align: middle
								
