section.pages-favourites
	padding-top: 40px
	padding-bottom: 60px
	table
		width: 100%

		thead
			border: thin solid #cccccc
			th
				padding: 5px 0px
				text-align: center
				font-size: 14px
				color: #7b7b7b
				font-weight: 400
				&:not(:first-child)
					vertical-align: middle
					white-space: nowrap
		tbody
			tr
				td
					border: thin solid #cccccc
					font-weight: 400
					color: black
					text-align: center
					padding: 20px  15px
					&:first-child
						max-width: 700px
						min-width: 500px
					&:not(:first-child)
						vertical-align: middle
						word-break: break-all
					&:first-child
						text-align: left

		.box-sp
			display: flex
			align-items: flex-start
			box-shadow: none
			.box-sp-img
				position: relative
				width: 400px
				height: 200px
				img
					position: absolute
					width: 100%
					height: 100%
					left: 0
					top: 0
					border-radius: 10px
					object-fit: cover
				.tag-thuongluong
					border-radius: 5px 5px 5px 10px
			.box-sp-text
				width: 100%
				padding: 0px
				padding-left: 15px
				h5
					font-size: 20/16*1rem
					font-weight: 700
					color: $main-color
				.location
					padding: 15px 0px
				.end-mota
					border-top: 0px
					padding: 0px
					padding-left: 8px
	.box-favourites
		.col-md-6
			padding-top: 30px
		.box-sp-text
			.end-mota
				border-top: 0px
				border-bottom: thin solid #cccccc
				padding-bottom: 10px
		.info
			padding-top: 10px
			padding-bottom: 15px
			display: flex
			flex-direction: column
			.content
				display: flex
				justify-content: space-between
				position: relative
				margin-top: 15px
				p
					z-index: 99
					background-color: white
					min-width: 1px
					&:first-child
						padding-right: 15px
					&:last-child
						padding-left: 15px
						color: #273879
						font-weight: 500
				.dashed-line
					position: absolute
					left: 0%
					width: 100%
					border-bottom: 1px dashed #cccccc
					top: 50%
