section.pages-news-detail
	padding-top: 40px 
	padding-bottom: 60px
	.date 
		padding-left: 20px
		padding-bottom: 15px
		p
			font-size: 12px
			color: gray
			margin: 0px
			position: relative
			&::before
				position: absolute
				top: 25%
				left: -20px
				background: #ab843a
				width: 13px
				height: 5px
				content: ""
	.article-container 
		@media(max-width: 768px)
			padding-bottom: 30px
		.title 
			padding-bottom: 20px
			h1 
				color: $third-color
		.content
			p,b 
				padding: 12px 0px
			img 
				width: 100%
	.orther-news 
		.title 
			background: $third-color 
			padding: 10px 15px 
			border-radius: 5px 5px 0px 0px
			text-align: center 
			h2 
				color: white 
				font-size: 14/16*1rem 
				font-weight: 700
		.news-content  
			display: flex 
			padding: 15px 0px 
			border-bottom: thin solid #cccccc
			&:hover
				img 
					filter: blur(1.3px)
				.content
					h2 
						color: $secondary-color 
						text-decoration: underline
			.img 
				width: 100px
				height: 100%
				display: inline-table
				img 
					width: 100%
					height: 100px
					object-fit: cover
					transition: 0.3s all ease
			.content  
				padding-left: 10px
				display: flex 
				justify-content: space-between 
				flex-direction: column 
				
				.date
					padding-left: 20px
					padding-bottom: 0px
					p
						font-size: 14px
				h2 
					display: -webkit-box
					-webkit-line-clamp: 4 // s dng mun hin th
					-webkit-box-orient: vertical
					text-overflow: ellipse
					overflow: hidden 
					line-height: 1.1
					font-weight: 700
					transition: 0.3s all ease
	@media screen and ( min-width:  768px)
		.col-xl-3 
			padding-left: 20px
