// Animation  center
.form-wrap.input_animation
	padding: 40px 0px
	.form-group
		position: relative
		width: 100%
		height: 50px
		input
			width: 100%
			height: 100%
			color: #757575
			font-size: 14/16*1rem
			font-weight: 300
			padding-top: 25px
			border: none
			outline: none
			&:focus,&:valid
				+ label
					span
						transform: translateY(-110%)
						font-size: 12.5/16*1rem
						color: $main-color
					&::after
						width: 100%
		label
			position: absolute
			bottom: 0
			left: 0
			width: 100%
			height: 100%
			pointer-events: none
			border-bottom: thin solid #cccccc
			&::after
				content: ''
				position: absolute
				height: 100%
				width: 0%
				border-bottom: 2px solid $main-color
				left: 50%
				bottom: -10px
				transform: translate(-50%)
				transition: 0.3s all ease
			span
				position: absolute
				bottom: 0px
				left: 0px
				transition: 0.3s all ease
