button
	background-color: transparent
	&:focus
		outline: none
		box-shadow: none
	

.btn 
	border: 0
	padding: 0
	&:focus
		outline: none
		box-shadow: none
	
	&__new 
		font-size: 14px
		font-family: "Montserrat"
		color: rgb(255, 255, 255)
		font-weight: bold
		text-decoration: underline
		text-transform: uppercase
		line-height: 2
		text-align: left
		transition: .3s all ease-in-out
		// &:hover 
		// 	text-decoration: underline $main-color
		// 
		span
			transform: skewX(20deg)
			.ri-arrow-right-line
				font-size: 14px
				line-height: 20px
				padding-left: 10px
			
		
		@media(max-width: 991.98px) 
			width: 120px
			font-size: 14px
		
	
	&__new2
		width: 190px
		height: 40px
		border-radius: 20px
		background-color: #0b3155
		line-height: 40px
		text-align: center
		color: #fff
		font-size: 15px
		font-family: "Montserrat"
		&:hover
			background-color: #3361ae
			color: #fff
	&__header
		height: 36px
		font-size: 14px
		font-weight: 500
		-webkit-tap-highlight-color: transparent
		color: #1976d2
		cursor: pointer
		text-align: center
		border: 1px solid #1976d2
		width: 100px
		border-radius: 8px
		overflow: hidden
		margin-left: 11px
.button-hr 
	button
		width: 100%
		height: 40px
		display: flex
		align-items: center
		justify-content: center
		border: 1px solid #01a8e3
		color: #01a8e3
		font-size: 16px
		font-weight: 400
		transition: .25s ease
		&:not(:first-child)
			margin-top: 10px
			&:hover
				color: white
				background-color: #01a8e3
		@media ( max-width: 1200px )
			margin-top: 40px
.checked
	margin-bottom: 14px
	input
		display: none
		&:checked + label::before
			border: 2px solid #283593
		&:checked + label::after
			display: block
			position: absolute
			top: 10px
			left: 5px
			border: 1px solid #283593
			border-radius: 11px
			background: #283593
			width: 10px
			height: 10px
			content: " "
	label 
		cursor: pointer
		position: relative
		-webkit-tap-highlight-color: transparent
		color: #000
		cursor: pointer
		font-size: 14px
		&:hover::before 
			transform: scale(1.2,1.2,1)
		&::before
			display: inline-block
			position: relative
			top: 5px
			margin: 0 10px 0 0
			border: 2px solid #81968b
			border-radius: 11px
			background-color: transparent
			width: 20px
			height: 20px
			transform: scale3d(1,1,1)
			transition: transform 0.22s cubic-bezier(0,0,0.2,1) 0ms
			content: " "
.paginationSP
	width: 100%
	display: flex
	align-items: center
	justify-content: flex-end
	&-box
		height: 52px
		// width: 365 / 1367 * 100%
		border: 1px solid $border-form
		display: flex
		align-items: center
		border-radius: 5px
		padding: 0 15px
		justify-content: space-evenly
		margin-top: 15px
		ul 
			display: flex
			align-items: center
			padding: 0
			li
				list-style: none
				display: flex
				align-items: center
				&:not(:last-child)
					margin-right: 10px
				a 
					width: 38px 
					height: 37px 
					display: flex
					align-items: center
					justify-content: center
					color: #8d6e63
					border-radius: 5px
					overflow: hidden
					font-size: 14px
					font-weight: 500
					&.active
						background-color: $main-icon
						box-shadow: 1px 1px 2px #0000004d
						color: white
	.last-pg
		font-size: 14px
		font-weight: 500
		color: #8d6e63
		padding-left: 10px
	.button-s 
		font-size: 24.01px
		font-weight: 400
		cursor: pointer
		transition: .25s ease
		&:hover 
			color: $main-color
// form input 
.form-group-input
	display: flex
	align-items: center
	width: 100%
	height: 54px
	border: 1px solid $border
	border-radius: 10px
	margin-top: 30px
	transition: .25s ease
	&:focus 
		border: 2px solid #1a4bb7
	.box-left-se
		width: 47px
		display: flex
		align-items: center
		justify-content: center
	.box-mid-se
		display: flex
		align-items: center
		flex: 1
		input 
			height: 40px
			width: 100%
			border: 0
			font-size: 16px
			transition: .25s 
			&:focus
				.form-group-input
					border: 2px solid #1a4bb7
					background-color: red
					
	&:hover
		border: 1px solid black
	
	.mk-icons
		color: $icon-grey
	.box-right-se
		width: 47px
		display: flex
		align-items: center
		justify-content: center
		span 
			cursor: pointer
.tag-thuongluong
