.box-left-admin,.user-admin
	border: 1px solid #dbdbdb
	border-radius: 10px
	overflow: hidden
	min-height: 500px
	.bl-1 
		background-color: #f5f5f5
		display: flex
		justify-content: center
		padding-top: 44px
		flex-direction: column
		align-items: center
		img 
			width: 100px
			height: 100px
			margin-bottom: 18px
			border-radius: 50%
			overflow: hidden
		p 
			color: #1c1c1b
			font-size: 24 / 16 * 1rem
			font-weight: 500
			letter-spacing: normal
			margin-bottom: 14px
	.bl-2 
		background-color: #f5f5f5
		padding: 0 25px
		padding-bottom: 30px
		span 
			display: flex
			align-items: center
			&.vifPay 
				color: #1a4bb7
				font-size: 14px
				font-weight: 700
				img 
					width: 19px
			&.lkvi
				color: #626262
				font-size: 12px
				margin-top: 8px
				margin-bottom: 10px
			&.lkngay
				text-align: right
				display: flex
				justify-content: flex-end
				a
					color: #1a4bb7!important
					cursor: pointer
					font-size: 14px
					font-weight: 400
					display: flex
					align-items: center
					span 
						font-size: 20px
			&.text 
				font-size: 13 / 16 * 1rem
				color: #2b2b2b
		img 
			width: 17px
			margin-right: 5px
	.bl-3 
		.title-bl3
			display: flex
			align-items: center
			height: 45px
			background-color: #e6f5f7
			padding-left: 18px
			span 
				color: #272727
				font-size: 30 / 16 * 1rem
				font-weight: 400
				margin-right: 17px
			p
				a
					color: #1a4bb7
					cursor: pointer
					font-size: 18 / 16 * 1rem
					font-weight: 600
		ul 
			padding: 0
			li 
				a 
					font-size: 1rem 
					color: #2b2b2b
					cursor: pointer
					list-style: none
					padding-left: 60px
					display: block
					&:not(:last-child)
						padding-bottom: 25px
					&:last-child
						padding-bottom: 13px
					&:first-child
						padding-top: 13px
					&:hover
						background-color: #f8f5f580
						color: #eac688
					&.active
						background-color: #f8f5f580
						color: #eac688
	.bl-4 
		display: flex
		align-items: center
		padding-left: 16px
		height: 46px
		border-top: 1px solid #dbdbdb
		p
			color: #2b2b2b
			cursor: pointer
			font-size: 18 / 16 * 1rem
			font-weight: 600
		span 
			color: #2b2b2b
			cursor: pointer
			font-size: 30 / 16 * 1rem
			margin-right: 15px