section.sanpham-s-2 
	.sanpham-wrapper
		position: relative
		.tag-thuongluong 
			padding-right: 10px
		.sanpham-slider 
			position: relative
			&::before
				content: ''
				width: 100%
				position: absolute
				top: 0px
				left: 0px
				width: 100%
				height: 6px
				background: #00000030
				z-index: 999
			.swiper-slide  
				.img-box 
					border: thin white solid
					img
						height: 38vh
						object-fit: fill
						@media (max-width: 1024px)
							height: 25vh
			.swiper-pagination 
				.swiper-pagination-bullet 
					width: 12px 
					height: 12px
				.swiper-pagination-bullet
					background: #cccccc
				.swiper-pagination-bullet-active 
					background: white
		.arrow-button 
			.swiper-button-next,.swiper-button-prev
				background-image: none
				transition: 0.3s ease-out
				span
					transition: 0.3s ease
					font-size: 3rem
					color: $white
					background-color: $third-color
					@media screen and ( max-width: 1024px )
						background-color: transparent
						color: black
				&:hover
					span
						background-color: $white 
						color: $third-color 
						border: thin solid $third-color 
						
			.swiper-button-prev 
				left: 0%
				width: 48px
				&:hover
					span 
						transition-duration: 0.5s
						transform: translatey(3px)
			.swiper-button-next 
				right: 0%
				width: 48px
				&:hover
					span 
						transition-duration: 0.5s
						transform: translatey(3px)
	.content 
		h1 
			padding: 25/16*1rem 0px	 10/16*1rem
		.info 
			display: flex  
			align-items: center   
			.cover 
				display: flex 
				align-items: center 
				justify-content: center
				&+.cover 
					padding-left: 10px
				span,p
					font-size: 14/16 *1rem 
					color: #cccccc 
				p 
					margin-left: 5px 
	.article-content
		.title
			position: relative
			padding: 20px 0px
			&::before
				content: ''
				position: absolute
				left: 0%
				width: 100%
				height: 1px
				top: 50%
				background-color: #cccccc
				z-index: -1
			h2 
				padding: 15px 0px 10px 
				font-size: 18px
				font-weight: 400px
				line-height: 1.5
				text-transform: uppercase
				padding: 0px 20px 0px 0px 
				display: inline
				background: $white
		p 
			font-size: 14px
			line-height: 1.5 
			color: black 
	.contact-info
		.title
			position: relative
			
			&::before
				content: ''
				position: absolute
				left: 0%
				width: 100%
				height: 1px
				top: 50%
				background-color: #cccccc
				z-index: -1
			h2 
				padding: 15px 0px 10px 
				font-size: 18px
				font-weight: 400px
				line-height: 1.5
				text-transform: uppercase
				padding: 0px 20px 0px 0px 
				display: inline
				background: $white
		.row 
			padding: 0px 15px
			.content 
				display: flex 
				justify-content: cenrer 
				align-items: center 
				padding: 15/16*1rem 0px
				font-size: 14/16*1rem 
				color: #2b2b2b
				p 
					padding-left: 10px
		
