.pages-introduce-sec-1
	background-color: #f5f5f5
	&.section
		padding-bottom: 80px !important
	.block-title
		text-transform: uppercase
		background-image: -webkit-gradient(linear,left top,right top,from($main-color),to(#670808))
		-webkit-background-clip: text
		-webkit-text-fill-color: transparent
		font-size: r(60px)
	.head-content
		max-width: 900px
		padding-bottom: 30px
		margin: 0 auto
		text-align: justify
		text-align-last: center
		font-weight: 400
		font-size: r(16px)
	.box-info
		margin-top: 30px
		.col-xl-4
			min-height: 200px
			margin-bottom: 16px
		.intro-pic
			img
				object-fit: cover
				width: 100%
				height: 100%
		.intro-content
			display: flex
			justify-content: center
			align-items: flex-start
			padding: 30px 20px
			background-color: white
			box-shadow: 0px 0px 25px #00000021
			height: 100%
			.icon
				font-size: r(35px)
				color: $third-color
				display: contents
			.wrapper
				display: flex
				flex-direction: column
				margin-left: 30px
				align-items: flex-start
				.title
					text-transform: uppercase
					font-size: r(18px)
					color: #f0374f
					position: relative
					font-weight: 500
					&::before
						position: absolute
						content: ''
						height: 1px
						width: 18px
						left: -20px
						top: 50%
						transform: translateY(-50%)
						background-color: #f0374f
				.content
					font-weight: 400
					line-height: 1.5
					font-size: r(15px)

.pages-introduce-sec-2
	background: #f6fafd
	.box-img
		.img
			min-height: 25vh
			width: 100%
			margin-top: 30px
			@media screen and ( min-width: 993px )
				margin-top: -110px
			img
				width: 100%
				height: -webkit-fill-available
				object-fit: cover
	.box-content
		.content-item +.content-item
			margin-top: 30px
		.content-item
			display: flex
			justify-content: flex-start
			align-items: flex-start
			flex-direction: column
			.icon
				font-size: r(40px)
				color: $third-color
				margin-bottom: 15px
			.title
				margin-bottom: 15px
				font-weight: 400
				font-size: r(24px)
				text-transform: uppercase
				letter-spacing: 0.5
				color: $third-color
			.content
				font-size: r(15px)
				color: $main
				line-height: 1.5
				font-weight: 400
