.index-sc5,.sanpham-list
	margin-bottom: 80px
.box-sp
	box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12)
	border-radius: 4px
	background-color: white
	overflow: hidden
	margin-bottom: 5px
	.box-sp-img
		height: 170px
		width: 100%
		position: relative
		img 
			width: 100%
			height: 100%
			object-fit: cover
		.tag-thuongluong
			background-color: rgba(26,75,183,0.8)
			height: 27px
			display: flex
			align-items: center
			padding: 0px 5px 0px 10px
			position: absolute
			bottom: 0
			left: 0
			border-radius: 5px
			overflow: hidden
			color: white
			font-size: 12px
			font-weight: 400
			z-index: 2
		.box-icon
			position: absolute
			top: 0
			right: 4px
			display: flex
			align-items: center
			justify-content: space-around
			z-index: 2
			i 
				color: white
				font-size: 24px
				font-weight: 500
				background-color: hsla(0,0%,100%,0.24)
				width: 33px
				height: 33px
				display: flex
				align-items: center
				justify-content: center
				cursor: pointer
				border-radius: 50%
				&:last-child
					margin-left: 5px
		.overlay
			position: absolute
			width: 100%
			height: 100%
			bottom: 0
			top: 0
			background: linear-gradient(180deg,rgba(43,43,43,0.68),rgba(43,43,43,0.18) 30%,rgba(43,43,43,0))!important
			z-index: 1
			pointer-events: none
	.box-sp-text
		height: 50%
		background-color: white
		padding: 8px
		.title-text
			font-size: 14px
			color: #2b2b2b
			font-weight: 600
			line-height: 19px
			margin-bottom: 6px
		.location
			display: flex
			align-items: center
			margin-bottom: 10px
			span
				font-size: 20px
				color: #626262
				margin-right: 3px
			p 
				font-size: 12px
				font-weight: 400
		.mota-place
			margin-bottom: 10px
			.mota-place-1
				width: 100%
				display: flex
				align-items: center
				&:last-child
					margin-top: 10px
				.mota-place-tt
					width: 33.33%
					display: flex
					align-items: center
					img 
						width: 15px
						height: 15px
						margin-right: 5px
					span 
						color: #616161
						font-size: 12px
						overflow: hidden
						display: -webkit-box
						text-overflow: ellipsis
						-webkit-box-orient: vertical
						-webkit-line-clamp: 1
						padding-right: 5px
						&.icons-15
							font-size: 15px
		.end-mota
			padding-top: 7px
			border-top: 1px solid rgba(0,0,0,0.15)
			margin-left: -8px
			margin-right: -8px
			padding-left: 8px
			padding-right: 8px
			.mota-end-box
				display: flex
				align-items: center
				.end-box-tt
					display: flex
					align-items: center
					width: 33.33%
					span 
						color: #616161
						font-size: 12px
						overflow: hidden
						display: -webkit-box
						text-overflow: ellipsis
						-webkit-box-orient: vertical
						-webkit-line-clamp: 1
						padding-right: 5px
						&.icons-15
							font-size: 15px
						&.chat 
							color: $secondary-color
							cursor: pointer
