.dangbaiviet
	padding-top: 24px
	padding-bottom: 24px
	.bootstrap-tagsinput 
		width: 100%
		height: 45px
		display: flex
		align-items: center
		background-color: #f3f3f3
		.tag 
			font-size: 12 / 16 * 1rem
			background-color: #284694
			padding: 5px
			height: 20px
			display: flex
			align-items: center
	.form-upload 
		padding: 20px 10px 0px 10px
		&__preview 
			display: flex
			flex-wrap: wrap
			width: calc( 100% + 15px )
			margin-left: -7.5px
		&__title 
			cursor: pointer
			display: inline-block
			padding: 10px 15px 9px
			text-align: center
			border-radius: 5px
			border: 1px solid black
			color: #284694
			font-weight: 600
			position: relative
		&__control 
			position: absolute
			width: 0
			height: 0
		&__item 
			width: 150px
			margin: 0 7.5px
			margin-bottom: 20px
		&__item-thumbnail 
			padding-top: 100%
			background-size: auto 100%
			background-position: center
			background-repeat: no-repeat
			border: 1px solid #d2d2d2
			border-radius: 4px
		&__item-name 
			word-break: break-all
			text-align: center
			margin-top: 8px
			font-size: 13px
		&__field 
			display: flex 
		.btn-clear 
			height: 45px 
			display: none
			padding: 0 10px
			border: 1px solid black
			font-weight: bold
			color: #284694
	.box-left-form-muaban
		input[type=number]::-webkit-inner-spin-button 
			transition: .25s ease
			width: 10px
			display: block
			height: 30px
		input[type=number]
			padding-right: 10px
			background-image: none!important
		.notedongia 
			font-size: 10 / 16 * 1rem
			padding-left: 5px
		.note
			font-size: 13 / 16 * 1rem
			color: #7b7b7b
		p.note 
			margin-top: 10px
		.note-wrap
			margin-top: 20px
	.thongtinlh
		margin-top: 30px
		.wrap-title
			.tt-line
				display: block
				font-size: 20 / 16 * 1rem
				font-weight: 500
				color: black
				position: relative
				margin-bottom: 20px
				span 
					background-color: white
					padding-right: 20px
				&::after
					content: ''
					width: 100%
					height: 0.3px 
					background-color: black
					position: absolute
					right: 0
					top: 50%
					transform: translateY(-50%)
					z-index: -1
			p 
				color: #000
				font-size: 14 / 16 * 1rem
				margin-bottom: 24px
				margin-top: 24px
		.form-group
			display: flex
			align-items: center
			span,i 
				margin-right: 10px
			i 
				font-size: 24px
			input 
				width: 100%
				height: 56px
				border: 0
				border-bottom: 2px solid $border
				transition: .2s ease
				&:focus
					border-bottom: 2px solid $third-color
	.loaitindang
		margin-top: 30px
		.title
			display: flex
			justify-content: space-between
			h5 
				color: #000
				font-size: 20 / 16 * 1rem
				font-weight: 500
			a 
				text-decoration: underline
				font-size: 14 / 16 * 1rem
		.wrap-vip 
			display: flex
			align-items: center
			margin-top: 30px
			@media screen and ( max-width: 992px )
				display: none
			.checked 
				margin-right: 27px
				@media screen and ( max-width: 575px )
					flex: 0 0 50%
					margin-right: 0
					justify-content: center
				label 
					color: #2b2b2b
					cursor: pointer
					font-weight: 700
					font-size: 1rem
				.vip1 
					color: rgb(153, 51, 147)
				.vip2
					color: rgb(221, 140, 67)
				.vip3
					color: rgb(177, 135, 52)
		.wrap-vip-mobile
			display: none
			@media screen and ( max-width: 992px )
				display: block
				margin-top: 10px
				margin-bottom: 20px
				.select2-selection__rendered
					padding-left: 10px
		.wrap-des-vip 
			display: none
			justify-content: space-between
			align-items: center
			padding: 8px 
			background-color: whitesmoke
			margin-bottom: 20px
			border-radius: 10px
			&.active
				display: flex
			.wrap-left 
				display: flex
				align-items: center
				.wrap-text 
					padding-left: 10px
					p 
						
						font-size: 14px 
						font-weight: 500
			.wrap-right 
				display: flex
				.wrap-text
					display: flex
					flex-direction: column
					justify-content: flex-end
					align-items: flex-end
					strong 
						font-size: 14px 
						color: #2b2b2b
						font-weight: 500
					del 
						color: rgba(43,43,43,.4)
						font-size: 14px
						text-decoration: line-through
					p 
						color: #2b2b2b
						font-size: 14px
						display: inline

		.wrap-moTaVip
			width: 100%
			background-color: #f3f6fb
			padding: 15px 15px 15px 30px
			display: none
			&.active
				display: block
			@media screen and ( max-width: 575px )
				display: none!important
			table 
				border-collapse:separate
  				border-spacing: 0 1em
				tr 
					td 
						padding-bottom: 5px
						&:first-child
							width: 50px 
						strong 
							font-size: 20 / 16 * 1rem
							color: #1d1d1d
							font-weight: 700
						span 
							font-size: 14 / 16 * 1rem
							color: #424242
						em 
							font-size: 14px
							font-weight: 700
							text-transform: uppercase
							font-style: normal
				.mb-30
					padding-bottom: 10px
			.img 
				height: 180px
				object-fit: cover
				display: flex
				justify-content: flex-end
				img 
					height: 100%
		.wrap-lich
			margin-top: 30px 
			.form-group 
				label 
					display: block
					color: #000
					font-size: 14px
					margin-bottom: 4px
				input
					width: 100%		
					height: 40px
					border: 1px solid #bbbbbb
					background-color: #f5f5f5
					border-radius: 5px 
					overflow: hidden
					padding: 0 10px
					&::-webkit-inner-spin-button 
						transition: .25s ease
						width: 10px
						display: block
						height: 30px
				.select2-container
					width: 100%!important
				.select2-selection--single
					width: 100%		
					height: 40px
					border: 1px solid #bbbbbb
					background-color: #f5f5f5
					border-radius: 5px 
					overflow: hidden
					padding: 0 10px
					display: flex
					align-items: center
				.select2-selection__arrow
					right: 10px!important
					height: 100%!important
					b 
						border-width: 6px 6px 0!important

		.tongThanhToan-box
			width: 100%
			padding: 18px 15px
			background-color: #f5f5f5
			border-radius: 8px
			overflow: hidden
			p 
				font-size: 14 / 16 * 1rem
				color: #2b2b2b
			.ttt-1,.ttt-2,.ttt-3
				display: flex
				justify-content: space-between
			.ttt-2
				margin-top: 10px
			strong 
				font-size: 14 / 16 * 1rem
				color: #2b2b2b
				font-weight: 700
		.wrap-button
			margin-top: 70px
			display: flex
			justify-content: flex-end
			input:not(:last-child)
				margin-right: 8px