.form-group-select
	display: flex
	align-items: center
	position: relative
	width: 100%
	height: 40px
	padding: 4px 16px
	cursor: pointer
	overflow: hidden
	border-radius: 10px
	transition: .25s ease
	&::before
		content: ''
		border: 1px solid black
		display: block
		position: absolute
		left: 0
		right: 0
		top: 0
		bottom: 0
		border-radius: 10px
		pointer-events: none
	&:active
		background-color: #969696a6
	&.s-small
		border-radius: 5px!important
		&::before
			content: ''
			border: 1px solid #ccc
			display: block
			position: absolute
			left: 0
			right: 0
			top: 0
			bottom: 0
			border-radius: 5px!important
			pointer-events: none
		.box-left-se
			i
				color: #767676
	.box-left-se
		height: 40px
		display: flex
		align-items: center
		padding-right: 6px
		i
			font-size: 20px
	.box-mid-se
		display: flex
		flex: 1
		align-items: center
		select
			border: 0
			appearance: none
			width: 100%
			background-color: #ffffff00
			color: #909090
			font-size: 14px
			cursor: pointer
			height: 40px
		.timnangcao
			font-size: 14px
			font-weight: 500
			color: black
			width: 100%
			text-align: center
			text-transform: uppercase
			display: block
	.box-right-se
		display: flex
		align-items: center
.box-menu-mobile
	width: 100%
	margin: 0 auto
	height: 91px
	border-radius: 10px
	overflow: hidden
	box-shadow: 0 2px 4px -1px rgba(0,0,0,0.2), 0 4px 5px rgba(0,0,0,0.14), 0 1px 10px rgba(0,0,0,0.12)
	margin-bottom: 30px
	z-index: 999
	background-color: white
	display: none
	margin-top: -65px
	.box-mobile-wrap
		display: flex
		align-items: center
		height: 100%
		.box-m
			flex: 0 0 33.33%
			height: 100%
			display: flex
			align-items: center
			justify-content: center
			a
				display: block
				width: 100%
				height: 100%
				display: flex
				flex-direction: column
				align-items: center
				justify-content: center
				&:active
					background-color: #ddd
			img
				width: 44px
				height: 44px
				margin-bottom: 8px
			p
				font-size: 14 / 16 * 1rem
				font-weight: 500
				color: #000
	@media screen and ( max-width: 1024px )
		display: block

