section.compare-c-1
	display: flex 
	justify-content: flex-end
	width: 100% 
	height: 100%
	.wrapper
		width: 100% 
		height: 100%
		text-align: right 
		display: flex 
		flex-direction: column 
		justify-content: flex-end
		.title-compare
			h3 
				font-size: 1rem 
				font-weight: 700
		.line-break
			padding: 20px 0px 
			position: relative 
			&::before
				left: 0%
				position: absolute 
				content: ''
				width: 100% 
				height: 1px 
				background: #cccccc
		.content 
			padding-bottom: 38px
			p 
				font-size: 14/16*1rem
				font-weight: 700
			p + p
				padding-top: 15px
			
			
