.thaydoithongtin
	padding-top: 10px 
	padding-bottom: 40px
	.box-right
		#nav-tab
			border-bottom: 0px
			margin-bottom: 30px
			a 
				font-size: 14 / 16 * 1rem
				font-weight: 500
				cursor: pointer
				color: #929292
				border: 0
				border-bottom: 2px solid transparent
				text-transform: uppercase
				white-space: nowrap
				&.active
					color: #1a237e
					border-bottom: 2px solid #1a237e
		.tab-content
			.form-wrap
				.text-f
					color: #4b4b4b
					cursor: default
					font-size: 1rem
					font-weight: 500
					letter-spacing: normal
				.img 
					width: 75px 
					height: 75px
			.anhdaidien
				.hinhdd 
					display: flex
					align-items: center
					border-bottom: 1px solid $border
					padding-bottom: 24px
					.wrap-img
						margin-right: 18px
						width: 75px
						height: 75px
						border-radius: 50%
						overflow: hidden
						position: relative
						img 
							width: 100%
							height: 100%
							object-fit: cover
						.wrap-input
							position: absolute
							bottom: 0
							background-color: #00000033
							left: 0
							height: 35%
							right: 0
							display: flex
							align-items: center
							justify-content: center
							z-index: 999
							cursor: pointer
							em 
								font-size: 18px
								color: white
					.text 
						p 
							color: #2b2b2b
							font-size: 18px
							font-weight: 500
						span 
							color: #888
							font-size: 14px
							font-weight: 400
			.bdb 
				border-bottom: 1px solid $border
				padding-bottom: 16px
			.d-flex
				display: flex
				align-items: center
			.checked 
				margin-right: 10px
				margin-bottom: 0
			p
				color: #2b2b2b	
			.thongtinform 
				input 
					width: 100%
					height: 55px
					border: 1px solid $border
					padding-left: 10px
					font-size: 14 / 16 * 1rem
					font-weight: 400
					border-radius: 5px
					padding-right: 10px
					transition: .25s ease
					&::-webkit-inner-spin-button 
						transition: .25s ease
						width: 10px
						display: block
						height: 30px
					&:focus
						border: 1px solid #134ab8
					&:hover
						border: 1px solid black
				.last-form
					input 
						padding-left: 45px
					.user
