				.mt-20
					margin-top: 20px
				.notemk
					color: #000
					font-size: 14 / 16 * 1rem
					margin-top: 16px
					@media screen and ( min-width: 1024px )
						white-space: nowrap
	#nav-profile
		input
			margin-bottom: 15px
		.tdmk-s
			display: flex
			align-items: center