section.sanpham-s1 
	border: thin solid #cccccc 
	height: auto 
	width: 100% 
	border-radius: 5px
	.sec-1 
		padding: 20px 
		border-bottom: thin solid #cccccc
		.sec-1-box 
			display: flex 
			align-items: center 
			justify-content: center 
			.avatar 
				max-width: 60px 
				margin-right: 20px
				img 
					max-width: 100% 
					height: auto 
			.content 
				width: calc( 100% - 80px ) 
				.content-1 
					font-weight: 700px 
					display: flex 
					align-items: center  
					.name 
						padding-right: 20px
						border-right: thin solid #cccccc
					.host 
						padding-left: 20px
						p 
							font-weight: 400 
							color: #ccc
				.content-2 
					padding: 10px 0px
					.vertical-line 
						width: 100% 
						height: 1px 
						background: #ccc
				.content-3 
					display: flex 
					align-items: center 
					justify-content: space-around
					@media (max-width: 1600px)
						flex-flow: wrap
						p 
							&:last-child
								margin-top: 10px
					@media (max-width: 767px)
						p 
							&:last-child
								margin-top: 0px
					p 
						display: flex 
						align-items: center 
						font-size: 14px 
						font-weight: 700
						span 
							padding-right: 10px
					p
						&:first-child
							color: #ff9800
							
					p  
						&:last-child
							color: $third-color
	.sec-2 
		padding: 15px 20px 
		.sec-2-box 
			h2 
				font-weight: 700
			.sec-2-content
				display: flex
				justify-content: space-between 
				
				align-items: center 
				padding: 15px 0px
				position: relative
				p 
					min-width: 1px
					background-color: white  
					padding: 10px 0px
					z-index: 99
				p 
					&:last-child
						text-align: right 
						text-align-last: right
						white-space: wrap 
						font-size: 14px 
						color: #273879
						max-width: 200px 
						line-height: 1.2
						@media (max-width: 1600px)
							max-width: 100px
						@media (max-width: 1200px)
							max-width: 80px
						@media (max-width: 768px)
							max-width: 200px
				.dashed-line 
					position: absolute 
					left: 0%
					width: 100%
					border-bottom: 1px dashed #cccccc
	.sec-3 
		.sec-3-box 
			h2 
				font-weight: 700
				background-color: #f0f9fb	 
				padding: 10px 20px 
			.sec-3-content
				padding: 10px 20px
				.line-text 
					display: flex
					justify-content: space-between
					align-items: center 
					position: relative
					&+.line-text 
						margin-top: 5px
					p 
						min-width: 1px
						padding: 10px 0px 
						background-color: white
						z-index: 99 
					p 
						&:last-child
							text-align: right 
							text-align-last: right
							white-space: wrap 
							font-size: 14px 
							color: #273879
							min-width: 1px
							max-width: 200px 
							@media (max-width: 1600px)
								max-width: 100px
							@media (max-width: 1200px)
								max-width: 80px
							@media (max-width: 768px)
								max-width: 200px
					.dashed-line 
						position: absolute 
						left: 0%
						width: 100%
						border-bottom: 1px dashed #cccccc
