.form-group-sl1
	.select2-container
		width: 100%!important
		.select2-selection--single
			min-height: 40px
			.select2-selection__rendered
				min-height: 40px
				display: flex
				align-items: center
				padding-left: 40px
			.select2-selection__arrow
				min-height: 40px
				display: flex
				align-items: center
		.select2-selection__choice
			padding: 0 5px
			color: rgba(0,0,0,0.87)
			background: #e0e0e0
			font-size: 14px
			border: 0
			overflow: hidden
			display: -webkit-box
			text-overflow: ellipsis
			-webkit-box-orient: vertical
			-webkit-line-clamp: 1
			max-width: 80%
	input[type="number"],input[type="text"]
		display: block
		width: 100%
		height: 40px
		border: 1px solid #aaa
		border-radius: 5px
		padding: 0 5px
.select-many 
	.select2-selection--multiple
		padding-left: 40px