.swiper-container 
	width: 100%
	height: 100%
.swiper-slide 
	display: -webkit-box
	display: -ms-flexbox
	display: -webkit-flex
	display: flex
	-webkit-box-pack: center
	-ms-flex-pack: center
	-webkit-justify-content: center
	justify-content: center
	-webkit-box-align: center
	-ms-flex-align: center
	-webkit-align-items: center
	align-items: center
.arrow-button 
	.swiper-button-next,.swiper-button-prev
		background-image: none
		span
			font-size: 3rem
			color: $white
			background-color: $third-color
		&:hover
			span
				background-color: $white
				color: $third-color
				border: thin solid $third-color
	.swiper-button-prev 
		left: 0%
		width: 48px
		&:hover
			span 
				transition-duration: 0.5s
				transform: translatey(3px)
	.swiper-button-next 
		right: 0%
		width: 48px
		&:hover
			span 
				transition-duration: 0.5s
				transform: translatey(3px)
.swiper-pagination 
	.swiper-pagination-bullet
		width: 12px
		height: 12px
		background: #cccccc
	.swiper-pagination-bullet-active
		background: white
.tooltip
	.tooltip-inner
		background: #616161
		font-size: 14/16*1rem 
		border-radius: 5px
	.arrow 
		&::before	
			border-bottom-color: #616161 
			

