.login 
	width: 100%
	min-height: 100vh
	background-image: url(../assets/bg/bg_login.png)
	background-size: cover
	background-position: 50%
.login-wrap
	width: 450px
	margin: 0 auto
	padding-top: 100px
	.logo
		width: 270px
		margin: 0 auto
		margin-bottom: 30px
	.box-login
		width: 100%
		background-color: #fff
		border-radius: 8px
		box-shadow: rgba(209,209,209,.5) 0 3px 21px 0
		color: #000
		font-size: 14 / 16 * 1rem
		margin-bottom: 30px
		padding: 16px 32px
		position: relative
		.form-group-input
			margin-top: 25px
			.box-left-se
				color: $main-icon
			
		.qmk-f
			display: flex
			align-items: center
			justify-content: space-between
			margin-top: 25px
			.qmk,.dktk
				color: #3b4151
				cursor: pointer
				font-size: 14 / 16 * 1rem
				font-weight: 700
			.dktk 
				color: #134ab8
		.title
			font-size: 24 / 16 * 1rem
			text-align: center
			font-weight: 700
			padding-top: 20px
		.logindef
			display: block
			text-align: center
			margin-top: 24px
			margin-bottom: 24px
		.button-loginSocial
			margin-bottom: 20px
			a 
				display: flex
				align-items: center
				border: thin solid $border
				border-radius: 20px
				overflow: hidden
				img 
					margin-right: 24px