.quanlytindang
	padding-top: 10px
	padding-bottom: 40px
	.qltd-wrap
		width: 1540px
		margin: 0 auto
	.box-right
		#nav-tab
			border-bottom: 1px solid #134ab8
			box-shadow: 0px 1px 0px 0px #0000002e
			a 
				font-size: 18 / 16 * 1rem
				font-weight: 500
				cursor: pointer
				color: #929292
				border: 0
				border-bottom: 2px solid transparent
				&.active
					color: #3480cc
					border-bottom: 2px solid #3480cc
		.tab-content
			margin-top: 10px
			.select2-selection--multiple
				padding-left: 10px!important
			.box-search
				height: 72px
				width: 100%
				display: flex
				align-items: center
				background-color: #f5f5f5
				margin-top: 20px
				border-radius: 5px 
				overflow: hidden
				.box-search-wrap
					width: 100%
					margin: 0
				.form-group
					margin-bottom: 0
					position: relative
					select
						font-size: 13 / 16 * 1rem
						font-weight: 400
						background-color: white
						appearance: none
						background: url("data:image/svg+xmlcharset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24'%3E%3Cpath fill='none' d='M0 0h24v24H0z'/%3E%3Cpath d='M12 16l-6-6h12z' fill='rgba(153,153,153,1)'/%3E%3C/svg%3E") no-repeat
						background-position: right 10px center		
			.box-content
				border: 1px solid $border
				border-radius: 10px
				margin-top: 20px
				overflow: hidden
				table 
					width: 100%
					border-collapse: collapse
					border-radius: 10px
					thead 
						tr 
							th
								border: 1px solid $border
								border-collapse: collapse
								color: #1a4bb7
								font-size: 14px
								font-weight: 700
								text-align: center
								word-break: break-word
					tbody 
						tr 
							td 
								border: 1px solid $border
								padding: 8px
								text-align: center
								&:first-child
									width: 576 / 1150 * 100%
									.img
										position: relative
										overflow: hidden
										border-radius: 5px
										height: 100%
										img 
											width: 140px
											height: 100%
											overflow: hidden
											object-fit: cover
										.iconVip
											position: absolute
											top: 0
											left: 0
											width: 34px
											border-radius: 0
											height: 40px
										.tag 
											position: absolute
											bottom: 0
											background: rgba(26,75,183,0.8)
											color: #fff
											font-size: 12px
											text-align: center
											padding: 7px 4px
											width: 100%
											word-break: break-word
									.text 
										text-align: left 
										.t-1
											color: #2b2b2b
											cursor: pointer
											font-size: 14px
											font-weight: 500
											text-align: justify
											word-break: break-word
										.t-2
											color: #80868b
											display: flex
											align-items: center
											span 
												font-size: 18 / 16 * 1rem
												margin-right: 2px
											p 
												font-size: 12 / 16 * 1rem
										.t-3
											display: flex
											margin-bottom: 5px
											p 
												font-size: 12px
												flex: 0 0 50%
											strong 
												margin-right: 5px
												color: #2b2b2b
										.t-4 
											display: flex
											align-items: center
											span 
												font-size: 14.4px 
												color: #80868b
												margin-right: 2px
											p 
												font-size: 12px



					
				
					

					

