section.pages-news-list
	padding-top: 50px
	padding-bottom: 80px
	.nav-news-list
		width: 100%
		margin-bottom: 25px
		nav
			width: 100%
			ul
				padding: 0
				display: flex
				justify-content: space-between
				align-items: center
				overflow: auto
				padding-bottom: 5px
				&::-webkit-scrollbar-track
					box-shadow: inset 0 0 6px rgba(255, 255, 255, 0)
				&::-webkit-scrollbar
					width: 2px
					height: 2px
				&::-webkit-scrollbar-thumb
					border-radius: 2.5px
					box-shadow: inset 0 0 6px rgba(255, 255, 255, 0)
					background-color: $main-color
				.nav-item
					display: inline-block
					&.active
						a
							background-color: $main-color
							color: white
					& + .nav-item
						margin-left: 10px
					a
						white-space: nowrap
						text-transform: uppercase
						min-width: 200px
						font-size: r(15px)
						padding: 7px 10px
						background-color: white
						color: $main-color
						font-weight: 500
						display: flex
						justify-content: center
						align-items: center
						border: thin solid $main-color
						border-radius: 30px

	h1
		color: $third-color
		font-weight: 400
		margin-bottom: 30/16*1rem
	.article-big
		width: 100%
		height: auto
		transition: all 0.3s ease
		&:hover
			img
				filter: brightness(0.7)
			.content
				a
					h2
						color: $secondary-color
						text-decoration: underline
		.img
			overflow: hidden
			a
				+img-ratio(365/547)
		img
			transition: 0.3s all ease
		.content
			background-color: #024b74
			padding: 35px 30px
			height: 248px
			overflow: auto
			h2
				color: $white
				transition: all 0.3s ease

			p
				display: -webkit-box
				-webkit-line-clamp: 3 // s dng mun hin th
				-webkit-box-orient: vertical
				text-overflow: ellipse
				overflow: hidden
				padding-top: 12px
				text-align: left
				text-align-last: justify
				color: $white
				font-size: 14px
				font-weight: 300
				@media screen and ( max-width: 768px )

					text-align-last: left
	.sec-1
		@media screen and ( min-width: 768px )
			.wrapper
				padding-left: 20px
		@media screen and ( max-width: 768px )
			.wrapper
				padding-top: 30/16*1rem
	.wrapper
		display: flex
		align-items: center
		justify-content: space-around
		flex-direction: column
		.article-wrapper
			display: flex
			justify-content: flex-start
			flex-direction: row
			height: 100%
			&:hover
				.date
					p
						padding-bottom: 5px
						color: #b4b4b4
						&::before
							background: #25c140
							height: 15px
			&:not(:last-child)
				margin-bottom: 30px
			.date
				p
					transition: 0.3s ease all
					font-size: 14px
					color: black
					margin: 0px
					transform: rotate(180deg)
					writing-mode: vertical-lr
					position: relative
					padding-left: 2px
					&::before
						content: ""
						position: absolute
						left: 12px
						background: $secondary-color
						width: 6px
						height: 12px
						top: -25%
						transition: 0.3s all ease
			.article-small
				height: 100%
				display: flex
				flex-direction: row
				&:hover
					img
						filter: brightness(0.8)
					.content
						a
							h2
								color: $secondary-color
								text-decoration: underline
				&:not(:last-child)
					padding-bottom: 30px
				.img
					display: flex
					flex: 1
					img
						max-height: 291.5px
						transition: 0.3s all ease
						width: 100%
						height: 100%
						object-fit: cover
				.content
					flex: 1
					margin-left: 20px
					display: flex
					justify-content: center
					flex-direction: column
					a
						flex-grow: 0!important
						h2
							transition: all 0.3s ease
							padding-bottom: 10px
					p
						@media (max-width:992px )
							display: -webkit-box
							-webkit-line-clamp: 5 // s dng mun hin th
							-webkit-box-orient: vertical
							text-overflow: ellipse
							overflow: hidden
				&+.artical-small
	.row + .row
		padding-top: 30px
	.sec-2
		.wrapper
			padding-bottom: 30/16*1rem
			.article-wrapper
				.article-small
					flex-direction: column
					display: flex
					.img
						min-height: 224px
						height: 224px
					a
						display: block
					img
						max-width: 100%
					.content
						margin-left: 0px
						h2
							padding-top: 15px
							padding-bottom: 10px
						p
							display: -webkit-box
							-webkit-line-clamp: 3 // s dng mun hin th
							-webkit-box-orient: vertical
							text-overflow: ellipse
							overflow: hidden
	@media screen and ( min-width: 768px )
