
.index-s1
    background-image: url(../assets/bg/group6.svg),linear-gradient(118deg,#1d214a -18%,#273879 22%,#214094 83%)
    background-repeat: no-repeat,repeat
    background-position: 15% 100% 
    padding-bottom: 15px
    @media screen and ( max-width: 1024px )
        display: none
    .title-banner
        -webkit-tap-highlight-color: transparent
        -webkit-text-fill-color: transparent
        background-clip: text
        background-image: linear-gradient(90deg,#986e2c -15%,#fffacb 2%,#cfa352 113%,#926828 146%)
        color: #fff
        font-size: 37.6667px
        font-weight: 700
        letter-spacing: -.01562em
        padding: 0 0 17px
        @media screen and ( max-width: 1440px )
            font-size: 24px
        @media screen and ( max-width: 1280px )
            font-size: 20px
    .text-banner
        -webkit-tap-highlight-color: transparent
        color: #dab868
        font-size: 19px
        font-weight: 400
        letter-spacing: .51px
        line-height: 1.31
        @media screen and ( max-width: 1440px )
            font-size: 13px
        @media screen and ( max-width: 1280px )
            font-size: 10px
            
    .pd-banner
        padding: 48px 0px 
        margin-bottom: 48px
    .box-left-banner
        padding-left: 15px
        padding-right: 30px
    .slide-banner
        .img-banner
            border-radius: 10px 
            overflow: hidden
            img
                width: 100%
                height: 100%