section.pages-sanpham.pt-30
	padding-bottom: 30px
	@media screen and ( min-width: 768px  )
		.max-width-container 
			padding-bottom: 60px
	.max-width-container 
	@media screen and ( max-width:  768px)
		.sec-1 
			order: 2 
		.sec-2 
			order: 1 
		.max-width-container  
			border-bottom: 0px
			.button-sm 
				display: flex!important
				height: 60px
				align-items: center
				justify-content: center
				background-color: white
				position: fixed
				z-index: 999
				width: 100% 
				right: 0
				left: 0
				bottom: 0px
				.btn 
					border: thin solid $third-color
					a 
						padding: 2px 15px
					&:hover
						background-color: $third-color
						a
							color: white!important
					&+.btn 
						margin-left: 15px

