.global-breadcrumb
	width: 100%
	background-color: $third-color
	height: 60px 
	display: flex 
	align-items: center 
	justify-content: center
	.breadcrumb
		display: flex
		align-items: center
		background-color: transparent
		flex-wrap: wrap
		padding: 10px 0
		list-style: none 
		align-items: center
		margin-bottom: 0
		li
			position: relative
			padding: 0 6px
			display: flex
			align-items: center
			line-height: 0px
			a
				font-size: 18px
				color: white
				font-weight: 700
				text-decoration: none
				span 
					color: #e0c388
					padding-left: 5px
					font-weight: 400 

			&:first-child
				padding: 0px !important
				a 
					span 
						padding-left: 0px
			&.active
				a 
					color: #e0c388
			&:hover
				a
					color: #e0c388
			&:first-child
				a
		li 
			&:nth-of-type(3)
				&::before
					content: '|'
					color: white
					position: absolute
					font-size: 15px
					display: inline-block
					left: -6px
					padding-left: 5px
					top: -1px
	@media(min-width: 768.98px)
		margin-bottom: 0

	@media(min-width: 320px)
		margin-bottom: 0
