
.global-breadcrumb
	@apply flex
	position: relative
	z-index: 1
	height: 70px
	padding:  0
	background: linear-gradient(90deg,#273879 0%,#214094)
	@media screen and(max-width: 1024px)
		height: 50px
		background: #F3F3F3
	.breadcrumb
		padding: 16px 0px
		display: flex
		&-item 
			color: red   
			& + .breadcrumb-item
				&:before
					color: white 
					content: "\EBDB"
					font-family: remixicon!important
					font-size: 14px
					@media screen and ( max-width: 1024px )
						color: $third-color
	.sll-breadcrum
		@media screen and ( max-width: 1024px )
			display: none
	.container
		height: 60px
		@apply flex  flex-col items-start justify-center
	ol
		white-space: nowrap
		padding: 0
		list-style-type: none
		li
			display: inline-flex
			font-size: 16px
			line-height: 23px
			padding-right: 30px
			position: relative
			&:first-child
				&:after
					content: ''
			&:nth-child(2)
				overflow: hidden
				text-overflow: ellipsis
				-webkit-box-orient: vertical
				-webkit-line-clamp: 1
				flex: 1
				@media screen and ( max-width: 1024px )
					p 
						color: $third-color
			&.active
				color: $main
			a
				font-size: 1rem
				text-decoration: none
				line-height: 23px
				color: #585858
				display: flex
				align-items: center
				i 
					margin-right: 10px
					color: #9e9e9e
				@apply text-blue-4
				*
					font-family: 'Roboto'
				&.active
					color: #585858
					&:after
						color: #585858
	@media screen and(max-width: 1200px)
		margin-bottom: 0
	@media screen and(max-width: 768px)
		margin-bottom: 0
	@media screen and(max-width: 1024px)
		.breadcrumb
			li
				font-size: 14px
				line-height: 22px
				color: #888!important
				a
					font-size: 13 / 16 * 1rem
					line-height: 22px
					color: #888!important
					span
						font-size: 13 / 16 * 1rem
