section.compare-c-2 
	.compare-wrapper
		.title-compare 
			padding-bottom: 25px
			h2
				font-size: 28/16*1rem
				font-weight: 400
		.sanpham-container
			
			.sanpham-wrapper
				width: 100% 
				height: auto 
				display: flex 
				flex-wrap: nowrap 
				overflow-x: auto 
				padding: 0px 5px
				&::-webkit-scrollbar  
					width: 100%
					height: 10px
					background: #ccc
					border-radius: 10px
				&::-webkit-scrollbar-thumb 
					border-radius: 10px 
					background: rgb(238,174,202)
					background: radial-gradient(circle, $third-color 0%, rgba(148,187,233,1) 100%)
				.box-sp 
					flex: 0 0 auto
					max-width: 320px
					margin-bottom: 20px
					&:last-child
						margin-right: 15px
					&+.box-sp 
						margin-left: 30px
					.end-mota 
						border-top: 0px 
						padding-bottom: 10px 
						padding-top: 0px
					.location 
						padding: 10px 0px
					.info 
						.cover 
							position: relative
							width: 100%  
							height: auto 
							display: flex 
							align-items: center
							padding-top: 15px
							font-weight: 700 
							color: $third-color
							.dashed-line
								position: absolute 
								left: 0px 
								border-bottom: thin dashed #cccccc
								width: 100%  
							p 
								display: inline 
								background-color: white
								z-index: 10
								padding-right: 5px
								font-size: 14px
							


