header
.bg-menu
	position: fixed
	top: 0

	width: 100%
	height: 100%
	z-index: 2
	transition: ease-in-out all .3s
	background: transparent
	pointer-events: none
	&.active
		background: rgba(0, 0, 0, 0.4)
		left: 0
		pointer-events: auto
		transition: all .5s cubic-bezier(0.4, 0, 1, 1)
.top-bar-admin
	background-color: #181818
	width: 100%
	height: 40px
	color: white
	.max-width-container 
		display: flex
		align-items: center
		justify-content: space-between
		height: 100%
		@media screen and ( max-width: 768px )
			justify-content: center
		.truycapadmin 
			border-radius: 20px
			background-color: #d5b37a
			padding: 5px 10px
			text-transform: uppercase
			color: white
		.wrap-text-right
			display: flex
			align-items: center
			@media screen and ( max-width: 768px )
				display: none
			.line 
				margin: 0 10px
				background-color: #fff
				width: 2px
				height: 20px
	.dangxuat
		border-radius: 20px
		background-color: #f43c4e
		padding: 3px 10px
		color: white
.wrap-modal-dangnhap
	.modal-content 
		background-color: white
		border-radius: 5px
		padding: 20px
		.title 
			font-size: 1.25rem
			font-weight: 500
			line-height: 2rem
			letter-spacing: 0.0125em
			color: black
			margin-bottom: 10px
		p 
			font-size: 14 / 16 * 1rem
			line-height: 1.2
			color: black
		.btn-huy, a 
			color: rgba(26,75,183,.8)
			cursor: pointer
			font-size: 14px
			font-weight: 500
			justify-content: center
		.btn-huy 
			border: 0
			outline: 0
			margin-right: 10px
			text-transform: uppercase
		.wrap-button
			display: flex
			align-items: center
			justify-content: flex-end
			margin-top: 15px
			a 
				text-transform: uppercase
			
	.modal-dialog
		max-width: 400px
		margin-top: 40vh
.wrap-modal-post
		.modal-dialog 
			max-width: 400px
			margin-top: 20vh
		.modal-content 
			background-color: white
			border-radius: 5px
			padding: 20px
			padding-bottom: 30px
			.box 
				display: flex
				border: 1px solid #E0E0E0
				border-radius: 5px
				align-items: center
				padding: 0px 16px
				height: 48px
				justify-content: flex-start
				cursor: pointer
				transition: .3s all  ease-in-out
				img 
					width: 30px
					height: 24px
					margin-right: 20px
				p 
					font-size: 17 / 16 * 1rem
					color: #2b2b2b
					flex: 1
				em 
					color: rgba(26,75,183,.8)
					font-size: 24 / 16 * 1rem
				&:hover 
					background-color: #cccccc62
				&+.box 
					margin-top: 20px
header
	position: fixed
	width: 100%
	z-index: 999
	background-color: #ffffff
	.thong-bao
		position: relative
		.thong-bao-num 
			position: relative
		.number-tb
			position: absolute
			right: -15px!important
			border: 1.4px solid #fff
			color: #fff
			background: #e7843f
			border-radius: 50px
			font-size: 9px
			padding: 5px
			height: 12px
			top: -5px
			display: flex
			align-items: center
			justify-content: center
		.wrap-list-thongbao
			position: absolute
			top: 50px
			right: 0
			display: none
			width: 333px
			background-color: #fff
			box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12)
			outline: 0
			border-radius: 4px
			z-index: 6000
			.wrap-1
				display: flex
				justify-content: space-between
				align-items: center
				border-bottom: 1px solid rgba(0,0,0,0.12)
				padding: 10px 16px
				p 
					color: #000
					font-size: 16px
					letter-spacing: .00937em
					font-weight: 700
				em 
					color: black
					cursor: pointer
			.wrap-2
				min-height: 280px
				.khong-thong-bao 
					height: 100%
					display: none
					justify-content: center
					align-items: center
					height: 280px
					flex-direction: column
					p 
						font-size: 20px
						font-weight: 400
						margin-top: 20px
				.co-thong-bao 
					.item 
						padding: 10px 18px
						border-bottom: 1px solid rgba(0,0,0,0.12)
						position: relative
						.wrap-text
							a 
								color: #333333	
								overflow: hidden
								display: -webkit-box
								text-overflow: ellipsis
								-webkit-box-orient: vertical
								-webkit-line-clamp: 3
								color: #222222
								font-size: 1rem
								font-weight: 400
							.date 
								color: #999999
								font-size: 14px
								font-weight: 400
							.thongbao
								padding: 2px 10px
								font-size: 12px
								margin-bottom: 5px
								display: inline-block
								color: white
								border-radius: 5px
								&.post-expired
									background-color: #fd2e5e
								&.thongbao-color
									background-color: #ffd000
								


	.yeuthich 
