	@media screen and ( max-width: 1024px )
		.container-mb
			max-width: 100%
			padding: 0px
	&.active
		transform: translateY(-100%)
		transition: all 1s ease-in-out
	.nav-desktop
		display: flex
		align-items: center
		padding: 10px 0
		width: 100%
		@media screen and ( max-width: 1024px)
			padding: 10px 0
			display: none
		.logo
			max-width: 275px
			height: 80px
			@media screen and ( max-width: 1199px)
				max-width: 200px
			img
				width: 100%
				height: 100%
				object-fit: contain
				filter: drop-shadow(1px 1px 1px black)
		.main-nav
			display: flex
			flex: 1
			justify-content: flex-end
			width: 100%
			.Module
				display: flex
				width: 74%
				.ModuleContent
					width: 100%
					.nav
						width: 100%
			.nav-list
				padding-left: 0
				display: flex
				align-items: center
				.nav-item
					margin-left: 9px
					display: flex
					align-items: center
					flex-direction: column
					height: 51px
					padding: 0 10px
					justify-content: center
					list-style: none
					.avatar-login
						width: 34px
						height: 34px
						margin-left: 32px
					.avatar-user
						cursor: pointer
						height: 40px
						width: 40px
						border-radius: 50%
						overflow: hidden
				.icon
					color: $main-icon
					-webkit-font-smoothing: antialiased
					-webkit-tap-highlight-color: transparent
					cursor: pointer
					font-size: 22px
					font-weight: 400
					line-height: 1
					margin-bottom: 8px
					text-align: center
					text-transform: none
				.text
					-webkit-tap-highlight-color: transparent
					color: #4b4b4b
					cursor: pointer
					font-size: 12px
					font-weight: 500
					text-align: center
				.button-change-lang
					display: flex
					align-items: center
					width: 100%
					height: 100%
					padding: 0 10px
					border-radius: 25px
					cursor: pointer
					transition: .25s ease
					position: relative
					&:active
						background-color: #bfbfbf90
					i
						color: #8e8e8e
						font-size: 20px
						margin-left: 10px
					.list-change-lang
						position: absolute
						top: 45px
						right: 10px
						width: 195px
						min-height: 30px
						display: flex
						border-radius: 5px
						flex-direction: column
						background-color: white
						box-shadow: 0 1px 5px rgba(0,0,0,0.2), 0 2px 2px rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12)
						opacity: 0
						transition: .15s ease
						pointer-events: none
						&-row
							width: 100%
							height: 32px
							display: flex
							align-items: center
							padding-left: 15px
							transition: .25s ease
							img
								margin-right: 16px
							p
								-webkit-tap-highlight-color: transparent
								color: #000
								cursor: pointer
								font-size: 14px
								max-width: 100%
							&:hover
								background-color: #cccaca4d
						&.active
							opacity: 1
							pointer-events: visible
				.post-new
					list-style: none
					display: flex
					margin-left: 22px
					.text
						-webkit-tap-highlight-color: transparent
						color: #4b4b4b
						cursor: pointer
						font-size: 14px
						font-weight: 500
						margin-left: 3px
						text-align: center
						text-decoration: underline
					&:hover
						.nav-link
							color: $main-color
					&.active
						.nav-link
							color: $main-color
				@media screen and ( max-width: 1199px)
					.nav-item
						margin-left: 15px
						.nav-link
							font-size: 14px
	.nav-mobile
