section.lienhe-l-1
	padding-top: 40px
	.container
		max-width: 1230px!important
		h1
			color: $third-color
			font-weight: 400
		.row
			&.form-wrap
				padding: 0px
				@media screen and ( max-width: 1200px )
					.col-sm-6,.col-xl-12
						padding-right: 10px!important
			.col-xl-5
				// border: thin solid #cccccc 
				// box-shadow: -1px 2px 5px #0000005c
				background: white
			.form-group
				label
					bottom: 10px
					left: 20px
					border-bottom: none
					&::after
						display: none
				input
					font-weight: 500
					padding-left: 10px!important
				textarea
					padding-left: 15px
					padding-top: 15px
					height: 100%
					@media screen and ( min-width: 1200px )
						min-height: 170px
			.warpper-content
				padding: 20px
				.logo
					text-align: center
					img
						width: 35%
						padding-bottom: 30px
				h2
					color: $blue
				.info
					display: flex
					justify-content: flex-start
					flex-direction: column
					text-align: left!important
					.wrapper
						display: flex
						align-items: flex-start
						justify-content: flex-start
						padding-top: 20px
						span
							color: $blue
						p
							margin-left: 10px
			.wrapper-form
				@media screen and ( min-width:1200px  )
					padding-left: 15px
				p
					padding: 0px 0px 10px 0px
				.form-wrap
					input,textarea
						width: 100%
						border: none
					@media screen and ( max-width:1200px  )
						.col-sm-6,.col-xl-12
							padding: 0px 0px
				.button
					display: flex
					align-items: center
					justify-content: flex-end
					.btn
						padding: 5px 20px
						width: 210/16*1rem
						color: white
						transition: 0.3s ease-in
						&:hover
							&.btn-submit
								background: $blue
							&.btn-return
								background: #b4b4b4
					.btn-return
						background-color: #cccccc
					.btn-submit
						background-color: $third-color
						margin-left: 15px
