.dangky
	.login-wrap
		padding-top: 20px
	.dieukhoan
		font-size: 12 / 16 * 1rem
		margin-top: 40px
		a 
			color: #3959ed!important
.title
	display: flex
	align-items: center
	span 
		color: $icon-grey
		height: 36px
		display: flex
		align-items: center
		width: 48px
		justify-content: center
		border-radius: 10px
		cursor: pointer 
		transition: .25s ease
		&:hover
			background-color: $border-form
	p 
		display: flex
		flex: 1 
		justify-content: center
		padding-right: 48px