.logo_animation
	position: fixed
	top: 0
	left: 0
	right: 0
	display: flex
	align-items: center
	justify-content: center
	z-index: 9999
	bottom: 0
	overflow: hidden
	transition: 1s
	&::before
		content: ''
		position: absolute
		left: 0
		width: 50%
		height: 100%
		background-color: #fff
		transition: 1s
		z-index: 0
	&::after
		content: ''
		position: absolute
		right: 0
		width: 50%
		height: 100%
		background-color: #fff
		transition: 1s
	&.preload
		width: 100vw
		pointer-events: none
		opacity: 0
		transition: 0.5s all ease
		filter: blur(5px)
		display: flex
		align-items: center
		justify-content: center
		width: 100%
	&.preload::before
		left: -150%
		background-color: black
	&.preload::after
		right: -150%
		background-color: black
	.logo-wrapper
		display: flex
		align-items: center
		justify-content: center
		width: 50%
		position: relative
		height: 100%
		z-index: 999
		img
			pointer-events: none
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%,-50%)
			opacity: 0
			filter: drop-shadow(3px 4px 6px black)
			&.d-block
				opacity: 1
				transition: 1s
