section.sanpham-s-3 
	padding-top: 30px
	.article-none 
		display: flex 
		justify-content: center 
		align-items: center 
		flex-direction: column
		img 
			width: 30%
		p 
			font-size: 18px 
			font: 700 
			padding: 15px 0px 60px
	.article-wrapper
		padding: 20px 0px 60px 0px
		.swiper-pagination
			bottom: 0px
			border-radius: 15px
			background: #ececec82
			padding: 10px 10px
			width: 100px
			display: flex
			align-items: center
			justify-content: center
			left: 50% 
			transform: translate(-50%,-50%)
			.swiper-pagination-bullet
				width: 12px
				height: 12px
				background: #6380ea
			.swiper-pagination-bullet-active
				background: $third-color
